const uni = require('uni').default;



































import store from "../../store";

export default {
    data() {
        return {
            month: 1,
            income: 0,
            cost: 0,
            billItems: [{
                id: 1,
                userName: '我',
                accountType: '支付宝',
                accountName: '支付宝',
                amount: 10.00,
                income: false,
                time: '2018-08-17'
            }, {
                id: 2,
                userName: '我',
                accountType: '微信',
                accountName: '微信支付',
                amount: 12.00,
                income: false,
                time: '2018-08-17'
            }, {
                id: 3,
                userName: '我',
                accountType: '银行卡',
                accountName: '中国银行',
                amount: 10000.00,
                income: true,
                time: '2018-08-17'
            }],
            nodes: [{
                name: 'div',
                attrs: {
                    class: 'div_class',
                    style: 'line-height: 60px; color: black;text-align:left;margin: 10px 30px;'
                },
                children: [{
                    type: 'text',
                    text: 'first-bill-item'
                }]
            }]
        }
    },
    onShareAppMessage() {
        return {
            title: '欢迎使用仓鼠记账',
            path: '/pages/bill/bill'
        }
    },
    methods: {
        trigerCollapse(e) {
            for (let i = 0, len = this.lists.length; i < len; ++i) {
                if (e === i) {
                    this.lists[i].open = !this.lists[i].open;
                } else {
                    this.lists[i].open = false;
                }
            }
        },
        jumpToDetail(id) {
            uni.navigateTo({
                url: '/bill/bill/' + id
            })
        }
    },
    created() {
        const requestTash = uni.request({
            url: 'localhost:10002/api/bill/list',
            header: 'token: ' + store.state.token,
            success: function (res) {
                let param = res.data;

                // TODO @yanwenbo 数据格式化处理

                this.billItems = param;
                this.month = res.month;
                param.forEach(item => {
                    if (item.isIncome) {
                        this.billItems.income += item.amount;
                    } else {
                        this.billItems.cost += item.amount;
                    }
                });
            }
        });

    }
}



